/* ************************ */
/* ************************ */
/* search-section */
.search-section {
  padding: 7.9rem 3.6rem 0;
}

.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  position: relative;
}

.search-input {
  font-size: 2.4rem;
  height: 6.9rem;
  width: 100%;
  border: none;
  border-radius: 2rem;
  background-color: var(--color-dark--3);
}
.search-input::placeholder {
  transform: translateX(8.1rem);
  font-weight: 400;
}
.search-icon {
  height: 3.6rem;
  width: 3.6rem;
  position: absolute;
  left: 2.7rem;
}
.notify-icon {
  height: 4rem;
  width: 4rem;
}

/* ************************ */
/* ************************ */
/* carousel-section */
.carousel-section {
  /* background-color: aqua; */
  padding: 2.4rem 3.6rem 0;
}

.carousel-indicators li {
  border: none;
  transform: translateY(-0.5rem);
  height: 0.6rem;
  width: 1.4rem;
  border-radius: 0.8rem;
}

.carousel-caption-self {
  position: absolute;
  left: 7.53%;
  right: 0;
  bottom: 0;
  height: 100%;
}

.caption-self-box {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  /* background-color: antiquewhite; */
}

.carousel-title {
  font-family: "Microsoft YaHei";
  font-size: 5.78rem;
  margin: 0;
  text-shadow: 0.3rem 0.4rem var(--color-brand--3);
}
.carousel-text {
  font-size: 2.4rem;
  font-weight: 500;
  margin: 0;
  margin-top: 2rem;
  letter-spacing: 0.1rem;
}
.carousel-time {
  font-size: 2.4rem;
  font-weight: 500;
  margin: 0;
  margin-top: 1.7rem;
  background: linear-gradient(
    to right,
    var(--color-brand--4),
    var(--color-brand--5),
    rgba(255, 255, 255, 0)
  );
  align-self: flex-start;
  padding: 0.8rem 1rem;
  transform: translateX(-1rem);
}
.carousel-img {
  display: block;
  width: 36.3rem;
  height: 13.9rem;
  position: absolute;
  right: 0.5rem;
  bottom: 3.6rem;
}
