/* king-section */
.king-section {
  /* background-color: aqua; */
  padding: 2.4rem 3.6rem;
}
.kings-box {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  row-gap: 4.6rem;
}
.king-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
}
